ion. In this paper, we introduce RoleLLM, a framework to benchmark, elicit, and enhance role-playing abilities in LLMs. RoleLLM comprises four stages: (1) Role Profile Construction for 100 roles; (2) Context-Based Instruction Generation (Context-Instruct) for role-specific knowledge extraction; (3) Role Prompting using GPT (RoleGPT) for speaking style imitation; and (4) Role-Conditioned Instruction Tuning (RoCIT) for fine-tuning open-source models along with role customization. By Context-Instruct and RoleGPT, we create RoleBench, the first systematic and fine-grained character-level benchmark dataset for role-playing with 168,093 samples. Moreover, RoCIT on RoleBench yields RoleLLaMA (English) and RoleGLM (Chinese), significantly enhancing role-playing abilities and even achieving comparable results with RoleGPT (using GPT-4).\n      ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"cover"},[_c('h2',[_vm._v("Basic Statistics of RoleBench")]),_c('img',{staticClass:"img",attrs:{"src":require("@/assets/img6.png"),"alt":""}}),_c('img',{staticClass:"img",attrs:{"src":require("@/assets/img7.png"),"alt":""}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"video"},[_c('h2',[_vm._v("\n          Demonstration\n        ")]),_c('img',{staticClass:"img",attrs:{"src":require("@/assets/img_1.png"),"alt":""}}),_c('img',{staticClass:"img",attrs:{"src":require("@/assets/img_2.png"),"alt":""}}),_c('img',{staticClass:"img",attrs:{"src":require("@/assets/img_4.png"),"alt":""}}),_c('img',{staticClass:"img",attrs:{"src":require("@/assets/img_5.png"),"alt":""}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"method"},[_c('h2',[_vm._v("\n        Method\n      ")]),_c('img',{staticClass:"img",attrs:{"src":require("@/assets/img_3.png"),"alt":""}}),_c('p',{staticClass:"text_p"},[_vm._v("\n        Illustratio