<template>
<div class="chat">
  <div class="chat_box">
    <div class="chat1" v-if="chat1">
      <span class="chat_title">
        {{chat1}}
      </span>
      <img class="chat_img" :src="chat1Img" alt="">
    </div>
    <div class="chat2" v-if="chat1">
      <img class="chat_img" :src="chat2Img" alt="">
      <span class="chat_title">
          <template v-if="!chat2">
        <i class="el-icon-loading"></i>
      </template>
          <template v-else>
            {{ chat2 }}
