            :label="item"
            :value="item">
        </el-option>
      </el-select>
    </div>
    <div class="btn_c check">
      <el-checkbox v-model="checked">启用检索增强</el-checkbox>
    </div>
    <div class="btn_c">
      <p v-for="(item,i) in list" :key="i" style="cursor: pointer"
         class="p_c"
         @click="send(item, '1')">
        {{i+1}}. {{item}}
      </p>
    </div>


