<script>
import axios from 'axios'
export default {
  name: "chat",

  data(){
    return {
      //, 'Jack Sparrow', 'Jack Tolerance', 'Peter Parker'
      options: ['华妃', '孙悟空'],
      value: '华妃',
      checked: false,
      input: '',
      chat1: '',
      chat2: '',
      list: [],
      mp3Url: '',
    }
  },
  created() {
    this.getQuestion();
    },
  mounted() {
    },
  computed:{
    chat2Img() {
      if (this.value === '华妃') {
        return require('@/assets/hua.jpeg')
      }else if (this.value === '孙悟空') {
        return require('@/assets/sun.jpeg')
      }else {
        return require('@/assets/icon.png')
      }
    },
    chat1Img() {
      return require('@/assets/chat1.jpg')
    },
    _isMobile() {
      // 获取用户代理字符串
      const userAgent = window.navigator.userAgent;

      if (/(android|iphone|ipad)/i.test(userAgent)) {
        console.log("当前设备是手机");
        return true
      } else {
        console.log("当前设备是客户端");
        return false
      }
    },
  },
  methods:{
    send(i, type) {
      // this.chat1 = JSON.stringify(this.input);
      console.log(3333,i)
      var data;
      if (this.mp3Url) {
        this.$refs.audioPlayer.pause();
      }
      if (i && type === '1') {
        data = JSON.stringify({
          'name': this.value,
          'prompt': i,
          'few-shot': !!this.checked
        });
        this.chat1 = i;
      }else {
        data = JSON.stringify({
          'name': this.value,
          'prompt': this.input,
          'few-shot': !!this.checked
        });
        this.chat1 = this.input;
      }

      this.chat2 = "";
      const _ = this;
      _.input = "";
      var xhr = new XMLHttpRequest();
      xhr.withCredentials = true;

      xhr.addEventListener("readystatechange", function(e) {

        if(this.readyState === 4) {
          _.chat2 = this.responseText;
        }
      });

      xhr.open("POST", "https://api.chatimation.com:11199/prompt?Content-Type=application%2Fjson%3Bcharset%3Dutf-8");
      xhr.setRequestHeader("Content-Type", "application/json");
      xhr.send(data);
    },
    play() {
      var data = JSON.stringify({
        'name': this.value,
        'prompt': this.chat2,
      });
      let _ = this;
      var xhr = new XMLHttpRequest();
      xhr.withCredentials = true;

      xhr.addEventListener("readystatechange", async function(e) {

        if(this.readyState === 4) {

          console.log(this.response)
          const blob = new Blob([this.response], { type:
                'audio/mp3' });
          // 创建URL对象
          _.mp3Url = URL.createObjectURL(blob);
          console.log(148, _.mp3Url, blob.type,)
        }
      });

      xhr.open("POST", "https://api.chatimation.com:11199/audio?Content-Type=application%2Fjson%3Bcharset%3Dutf-8");
      xhr.responseType = 'blob' ;
      xhr.setRequestHeader("Content-Type", "application/json");
      xhr.send(data);
    },
    clear() {
      this.input = "";
      this.chat1 = "";
      this.chat2 = "";
    },
    enter() {
      if (!this._isMobile) {
        this.send();
      }
    },
    getQuestion() {
      var data = JSON.stringify({
        'name': this.value,
      });
      this.chat2 = "";
      this.chat1 = "";
      const _ = this;
      _.input = "";
      var xhr = new XMLHttpRequest();
      xhr.withCredentials = true;

      xhr.addEventListener("readystatechange", function(e) {
        //
        if(this.readyState === 4) {
          console.log(this.responseText);
          _.list = JSON.parse(this.responseText);
          // _.chat2 = this.responseText;
        }
      });

      xhr.open("POST", "https://api.chatimation.com:11199/question?Content-Type=application%2Fjson%3Bcharset%3Dutf-8");
      xhr.setRequestHeader("Content-Type", "application/json");
      xhr.send(data);


      // xhr.open("POST", "https://api.chatimation.com:11199/question");
      // xhr.open("POST", "https://www.stormdragon.cn/question?Content-Type=application%2Fjson%3Bcharset%3Dutf-8");
      // xhr.setRequestHeader("X-Access-Token", "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJleHAiOjE2OTQxMjQzNjMsInVzZXJuYW1lIjoib3RoZXIifQ.7KPUg310bbeyMayQ1K43ecNWazZWYcLKXQ1sxSxQhos");
      // xhr.setRequestHeader("User-Agent", "Apifox/1.0.0 (https://apifox.com)");
      // xhr.setRequestHeader("Content-Type", "application/json");
      // xhr.setRequestHeader("Accept", "*/*");
      // xhr.setRequestHeader("Host", "https://www.stormdragon.cn/question");
      // xhr.setRequestHeader("Connection", "keep-alive");

      // xhr.send(data);

    },
  }
}
</script>

